// Colors
// ---------------------------------

// Default

$color-default-dark:                #041827;
$color-form-submit-button:          #041427;
$color-default-dark-opacity:        rgba(4, 24, 39, 50%);
$color-default-white:               #f2f5f6;
$color-additional-white:            #ffffff;
$color-white-opacity:               rgba(255, 255, 255, 0.7);
$color-default-blue:                #00b2ff;
$color-modal-overlay:               #03101B;
$color-accordion-button:            #c4c4c4;
$color-accordion-border:            rgba(248, 253, 255, 0.7);
$color-advantages-border:           #ebebeb;
$color-main-background:             #f8fdff;

// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;
$ff-roboto:                         "Roboto", "Arial", sans-serif;
$ff-roboto-condensed:               "Roboto Condensed", "Arial", sans-serif;
$ff-pt-mono:                        "PT Mono", "Courier New", monospace;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-770:                            770px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       2dppx;
